import { of } from "rxjs";
import { map } from "rxjs/operators";
import { state } from "../../../../src/index";
import { pipe } from "rxjs";

const todos$ = state({
  name: "todos",
  value: [
    {
      desc: "",
      check: true
    },
    {
      desc: "",
      check: true
    },
    {
      desc: "",
      check: false
    },
    {
      desc: "",
      check: false
    },
    {
      desc: "",
      check: false
    }
  ],
  actions: {
    checkItem: pipe(
      map(action => {
        const n = action.payload;
        const todos = this.value;
        return todos.map((item, i) => {
          if (i === n) {
            item.check = !item.check;
          }
          return item;
        });
      })
    ),
    /* delItem: function(payload, value) {
      const n = payload;
      const todos = value;
      todos.splice(n, 1);
      return todos.slice();
    },
    addItem: function(payload, value) {
      const todos = value;
      const item = payload;
      return todos.concat(item);
    } */
  }
});

const undoneCount$ = todos$.pipe(
  map(todos => {
    let _conut = 0;
    todos.forEach(item => {
      if (!item.check) ++_conut;
    });
    return _conut;
  })
);

export { todos$, undoneCount$ };
